.page {
  .container {
    margin-top: 100px;
    display: flex;
    align-items: center;
    gap: 30px;
    width: 100vw;
    .image {
      width: 50%;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
      }
    }
    .productImagePreloader {
      width: 50%;
      position: relative;
      overflow: hidden;
      &::before {
        content: "";
        position: absolute;
        left: 0px;
        top: 0px;
        width: 50%;
        height: 100%;
        background: #000;
        z-index: 2;
        animation-name: preloader-animation;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-direction: alternate;
      }
      img {
        display: block;
        margin: 0 auto;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
      }
    }
    .wrapp {
      display: flex;
      flex-direction: column;
      gap: 20px;
      font-family: "Nunito Sans";
      .title {
        font-weight: 700;
        font-size: calc(20px + 15 * (100vw / 1200));
        line-height: normal;
        margin-bottom: 5%;
      }
      .price {
        font-weight: 700;
        font-size: calc(14px + 6 * (100vw / 1200));
        line-height: normal;
        margin-bottom: 5%;
      }
      .buttons {
        display: flex;
        gap: 20px;
        .addToBagButton {
          width: 300px;
          height: 50px;
          // padding: 15px 70px;
          background: #000000;
          color: #fff;
          font-family: "Nunito Sans";
          font-weight: 700;
          font-size: calc(14px + 2 * (100vw / 1200));
          line-height: 25px;
          color: #ffffff;
        }

        .likeButton {
          background: none;
          svg {
            width: 30px;
            height: 30px;
          }
        }
      }
    }
  }
}
@keyframes preloader-animation {
  from {
    background: linear-gradient();
    transform: translateX(-100%);
    background: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.005),
      rgba(0, 0, 0, 0.006),
      rgba(0, 0, 0, 0.007)
    );
  }
  to {
    transform: translateX(180%);
    background: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.005),
      rgba(0, 0, 0, 0.006),
      rgba(0, 0, 0, 0.007)
    );
  }
}

@media (max-width: 767px) {
  .page {
    .container {
      flex-direction: column;
      gap: 30px;
      .wrapp {
        .buttons {
          .addToBagButton {
            width: 270px;
            height: 40px;
          }
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .page {
    .container {
      flex-direction: column;
      gap: 20px;
      .wrapp {
        .buttons {
          .addToBagButton {
            width: 230px;
            height: 35px;
          }
        }
      }
    }
  }
}
